@import "../node_modules/@tinper/next-ui/dist/tinper-next.css";
$prefix: wui;
.ac-input-locale{
    position: relative;
    min-width: 100px;
    min-height: 32px;
    line-height: 32px;
    
    .uf.uf-globe{
      font-size: 20px
    }
    .input-pop-icon{
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 10px;
    }
  
    .error{
      padding-left: 10px;
      color: red;
    }
    .popconfirm{
      width: auto;
    }
    .input-text{
      line-height: 32px;
      height: 32px;
    }
    .#{$prefix}-input[disabled] + .uf-globe{
        cursor: not-allowed;
    }
    .#{$prefix}-popover-content{
      padding: 16px 30px;
      background:rgba(252,252,252,1);
      border-radius:3px;
      border:1px solid rgba(223,225,227,1);
    }
    .#{$prefix}-row{
      padding-bottom: 7px;
    }
    .input-icon{
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 10px;
      font-size: 20px;
      // background: url("./assets/images/language.png") no-repeat center;
      background-size: 100%;
    }
    .input-icon:hover{
      // background: url("./assets/images/language_hover.png") no-repeat center;
    }
  }
  
  .input-locale-text{
    padding: 4px 0;
    display: flex;
    display: -ms-flexbox;
    display: -moz-box;
    display: -webkit-box
  }
  .input-locale-text-r{
    text-align:right;
    font-size:12px;
    font-weight:400;
    width:80px;
    color:rgba(71,77,84,1);
    padding-right:10px;
  }
  .input-locale-text-l{
    word-wrap: break-word;
    font-size:12px;
    font-weight:400;
    color:rgba(110,110,119,1);
    text-align:left;
    max-width:300px;
    padding-right:10px;
  }
  
  
  
  .ac-input-locale-popconfirm.#{$prefix}-popconfirm{
    min-width: 600px;
    width: 600px;
  }
  
  .ac-input-locale-modal,{
      .cancel-qx{
          margin-right: 8px;
          background: #fff
      }
      .cancel-qx:hover{
          color: rgb(33,33,33);
          background-color: #EBECF0;
          border-color: rgb(165, 173, 186);
      }
      .#{$prefix}-modal-header{
          padding:16px;
      }
    .#{$prefix}-popconfirm-confirm{
      text-align: right;
      padding-right: 30px;
    }
    .require-star{
      width: 16px;
      margin-right: 8px;
      color: #f53c32;
      display: inline-block;
    }
    .#{$prefix}-modal-content .modal-title{
      font-size:14px;
      font-weight:normal;
      color:rgba(71,77,84,1);
    }
    .#{$prefix}-modal-body{
      background: #F7F9FB;
    }
  
    .edit-panel {
      .padding-right-0{
        padding-right: 0;
      }
      
      .#{$prefix}-form-item {
          display: block;
          min-width: 100%;
          min-height: 58px;
  
          .#{$prefix}-form-item-label{
              display: inline-block;
              float: left;
              width: 130px;
          }
          .error {
              margin-left: 100px;
              color: #f53c32;
              display: block;
              padding-top:0;
              display: inline-block;
              font-size: 12px;
          }
          .#{$prefix}-label {
              display: inline-block;
          }
          .#{$prefix}-label.time {
              position: relative;
              bottom: 10px;
          }
          .#{$prefix}-label+* {
              min-width: 200px;
              display: inline-block;
          }
          .mast {
              width: 5px;
              min-width: auto;
              margin-right: 5px;
              color: red;
          }
          .datepicker-input-group {
              .#{$prefix}-input-group-btn {
                  .uf-calendar {
                      padding: 0;
                  }
              }
          }
          .#{$prefix}-switch {
              min-width: auto;
          }
          .#{$prefix}-select {
              width: auto;
          }
          .calendar-picker {
              display: inline-block;
              min-width: 200px;
              width: auto;
              input {
                  width: 100%;
              }
          }
          .#{$prefix}-input{
              width: 100%
          }
          .#{$prefix}-input-close {
              width: auto;
              .#{$prefix}-input {
                  width: 100%
              }
          }
      }
      .search-panel-btn {
          text-align: right;
          padding: 10px 0;
      }
    }
    .edit-panel-all {
      .#{$prefix}-form-item {
          padding: 0;
          .#{$prefix}-input{
              width: 100%
          }
          .#{$prefix}-label {
              width: 100%;
              text-align: right;
              font-size: 12px;
              height: 32px;
              line-height: 32px;
              padding: 0;
              display: inline-block;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              color: #212121;
          }
          @media (max-width: 992px) {
            .#{$prefix}-label {
              text-align: left;
            }
          }
          
          @media (max-width: 992px) {
            .#{$prefix}-input {
              width: 100%;
            }
          }
          .error {
              margin-left: 0;
          }
      }
    }
    .label-default{
      font-weight:400;
      font-size:14px;
      color:rgba(225,76,70,1);
      line-height:20px;
      padding-right:5px;
    }
  }
  
  
  
  
